/*********************
WRAPPER
*********************/
.wrap, .cb-wrap-off #cb-content { max-width: 720px; }

#logo, #mob-logo {
img { max-height: 100px; }
}

/*********************
GRID STYLES
*********************/
.cb-col-1 { width: 100%; }
.cb-col-2 { width: 100%; }
.cb-col-3 { width: 100%; }
.cb-col-4 { width: 33.33333333%; }
.cb-col-5 { width: 100% }
.cb-col-6 { width: 50%; }
.cb-col-7 { width: 100%; }
.cb-col-8 { width: 66.66666666%; }
.cb-col-9 { width: 100%; }
.cb-col-10 { width: 100%; }
.cb-col-11 { width: 100%; }
.cb-col-12 { width: 100%; }
.cb-col-1, .cb-col-2, .cb-col-3, , .cb-col-4, .cb-col-5, .cb-col-6, .cb-col-7, .cb-col-8, .cb-col-9, .cb-col-10, .cb-col-11, .cb-col-12 { float:left; }

.cb-col:nth-of-type(1) { padding-right: 20px; }
.cb-col:nth-of-type(2) { padding-left: 20px; }

/*------------------------------------------------------------------
[12. Modules]
*/
/******************************************************************
MODULE STYLES
******************************************************************/

.cb-grid-x {  
  .cb-s { width: 50%; }
  .cb-s, .cb-m { height: 190px; }
  .cb-m {
    .cb-byline { display: block; }
  }
	.cb-l { height: 300px;  }
 }

.cb-grid-3-sq { 
  .cb-s {    width: 33.1%;float: left;     border-bottom: 0; }
  .cb-feature-2 { border-right: 3px solid transparent; border-left: 3px solid transparent;     width: 33.8%; }
}

.cb-grid-3 {
  .cb-feature-2 { border-right: 3px solid transparent; }
}
.cb-grid-4 {
  .cb-feature-2 { border-right: 3px solid transparent; }
}

.cb-grid-5 {
  .cb-feature-2 { border-right: 3px solid transparent; }
  .cb-feature-4 { border-right: 3px solid transparent; }
}

.cb-grid-6 {
  .cb-feature-1, .cb-feature-3, .cb-feature-5 { border-right: 3px solid transparent; }
}

.cb-grid-3-static {
  .cb-feature-2 { border-bottom: 0; border-top: 0;  }
  .cb-feature-3 { border-bottom: 0; border-top: 0;  }
}
.cb-module-block {
	
	.cb-no-1 { clear: both;}
  .slides { .cb-no-1 { clear: none; } }
	.cb-article-row-2 { width: 48%; margin-left: 4%; }
	.cb-article-row-3 { width: 32%; margin-left: 2%; }
}

.cb-section-fs {
  .cb-grid-3 { 
    .cb-m { height: 300px; }
    .cb-feature-2 { border-bottom-style: solid; border-bottom-width: 3px;   border-right: 0; }
  }
}
.cb-module-header { 
  h2 { font-size: 26px; } 
}
.cb-module-half { width: 48%; margin-right: 4%; float:left; }
.cb-module-half:nth-child(even) { margin-right: 0!important; }

.cb-module-e { 
  .cb-mask { width: 34.6666%; float: left;  }
  .cb-meta { padding-left: 4%; width: 65.3334%; float: left; 
    .cb-post-title { margin-top: 0; }
  }
}

#cb-section-b .cb-module-block.cb-module-fr .cb-article-row { width: 48%; }
#cb-section-b .cb-module-block.cb-module-fr .cb-article-row.cb-no-2 { margin-left: 4%; }

.comment-author { 

        time { float:right;    }
}