/*------------------------------------------------------------------
[20. Content]
*/
/*********************
WRAPPER
*********************/
.wrap, .cb-wrap-off #cb-content { width: 990px; }

.cb-sidebar { width:300px; }
.cb-main { width: 625px; }
.cb-sidebar-left {
  .cb-sidebar { margin-right: 25px; }
}
.cb-sidebar-right {
  .cb-sidebar { margin-left: 25px; }
}
.cb-sidebar-none {
  .cb-main { width: 625px; }
}

.cb-sidebar-none.cb-sidebar-none-fw {
  .cb-main { width: 100%;   max-width: 980px; }
}

blockquote { margin: 40px; font-size: 26px; }



.cb-sidebar-none.cb-sidebar-none-fw {
   .cb-fis-title-bg {
    .cb-meta { width: 100%; }
  }
}
.cb-byline .price { font-size: 18px; }
/*********************
LOGO AND HEADER AD STYLES
*********************/

#cb-logo-box { padding: 20px; }
#logo, #mob-logo { width:auto; text-align: left; float: none; }
.cb-large { width: 728px; float: right; }
.cb-with-block { #logo img, #mob-logo img { max-width:210px; } }
.cb-block { margin-top: 0; }

.cb-entry-content { margin: 0 0 30px;

  .alignleft .wp-caption-text, .alignright .wp-caption-text { padding: 20px 30px;  }
  .alignnone.cb-fs-embed .wp-caption-text { top: 40px; right: 20px; padding: 20px 30px; position: absolute; background: #fff; background: rgba(255,255,255,0.8);max-width: 80%;  }

}


/*------------------------------------------------------------------
[21. Menus]
*/
/*********************
MENU STYLES
*********************/
#cb-nav-bar { font-size: 14px; letter-spacing: 1px;
    .cb-mega-menu { width: 990px; left: -20px; }
    .cb-main-nav { width: 950px;   }
    .cb-links-menu { font-size: 12px;
    .cb-sub-menu { width: 230px; }
    }
    #cb-trending-block { min-height: 307px; }
    .cb-big-menu {
    .cb-sub-menu { font-size: 14px; }
      .cb-mega-trending {
        ul {

          li {
            .cb-meta {
            .cb-post-title {
                  a { font-size: 16px;  }
            }
            .cb-post-views { font-size: 24px;  }
          }
        }
      }
    }
    }


    ul {
    .cb-trending {
      .cb-countdown {  padding: 7px 20px; font-size: 22px; }
    }
    }
    .cb-mega-menu-columns {   padding: 50px 0;
    .cb-sub-menu {

     > li > a { font-size: 18px; }
      li {
            a {  margin: 0 0 20px 0; }
            .cb-grandchild-menu {
                li {padding: 7px }
            }
      }


    }
    }
}

/******************************************************************
POST LIST STYLE A
******************************************************************/
.cb-post-list-a { margin: 0 0 30px 0; width:auto;

  .cb-mask{width:40%; margin: 0 3% 0 0;     }
  .cb-meta{ width:57%;  padding:10px 20px 10px 10px;
       h2 { margin: 0 0 10px 0; font-size: 20px; }
    .cb-excerpt { font-size:14px;}
  }
}

.cb-video-overlay-icon { height: 80px; width: 80px; line-height: 80px; font-size: 2em;  }

.cb-fis-big {
  .cb-no-mt {
    .cb-media-icon { margin-top: -40px; }
  }
}


/*------------------------------------------------------------------
[22. Modules]
*/
/*********************
MODULE STYLES
*********************/
#cb-section-f, #cb-section-g {
  .cb-slider {

    .cb-byline { font-size: 16px; letter-spacing: 5px; margin-top: 10px; }
    .cb-arrows-sides .flex-direction-nav li a { margin: 0 40px; }
   .cb-module-header { padding: 40px;
      .cb-module-title { font-size: 24px; }
    }
  }
}

.cb-gallery-post-wrap {
  .cb-arrows-sides .flex-direction-nav li a { margin: 0 30px; }
}

.cb-grid-x {  height: 498px; }

.cb-meta-style-2.cb-s {
  .cb-article-meta { padding: 0 30px; }
}




.cb-slider-2 {
  .cb-meta-style-2 {
    .cb-article-meta {
           .cb-byline { display: block; }
      }
  }
}
.cb-module-block {
  .cb-article-row { width: 300px; margin-left: 25px; margin-bottom: 25px; }
}
.cb-blog-style-roll #cb-blog-infinite-load, .cb-blog-style-roll .cb-no-more-posts span { margin-top: 40px; }
.instagram-pics { 
    li { width: 16.666667%; }
}
/*------------------------------------------------------------------
[23. Posts]
*/
/******************************************************************
FEATURED IMAGE STYLES
******************************************************************/

.cb-fis h1 { font-size: 30px;}

.cb-fis-block-video {
  .cb-meta {
    h1 { font-size: 36px; letter-spacing: 4px; }
  }
}

.cb-fis-block-site-width {  height: 500px; }

/*********************
POSTS FOOTER BLOCKS
*********************/

.commentlist {

	.comment {
		.cb-comment-body {
			.cb-gravatar-image { margin-right: 40px; }
			.comment-author { min-height: 70px;			}
			.comment_content, .alert p {  padding-left: 110px; }
		}

		#respond { padding-left: 40px; }
		.children {padding-left: 80px; }
	}
}

/*********************
AUTHOR BOX STYLE
*********************/

#cb-author-box {
  .cb-meta { padding-left: 40px;
    }

}

/******************************************************************
PREVIOUS/NEXT LINKS
*****************************************************************/
.cb-next-previous { padding: 40px;
  .cb-next-post {  padding-left: 10px;
    i { padding-left: 40px;  }
    .cb-next-title { display: block;  padding-right: 54px; }
  }
  .cb-previous-post {  padding-right: 30px;
    i { padding-right: 40px; }
    .cb-previous-title { display: block;  padding-left: 54px; }
    
  }
}

/*********************
REVIEW STYLES
*********************/

.cb-review-box.cb-half.cb-points-container, .cb-review-box.cb-half.cb-percentage-container {  margin:0 20px 20px 0;  float: left; width:50%;
    .cb-pros-cons-wrap { width: 100%; text-align: center; }
    .cb-pros-cons-wrap:before, .cb-pros-cons-wrap:after {content: ""; display: table; }
    .cb-pros-cons-wrap:after { clear: both; }
    .cb-review-title { text-align: center; }
    .cb-score-box { margin-top: 10px; -moz-transform: translateX(-50%); -ms-transform: translateX(-50%); -webkit-transform: translateX(-50%); transform: translateX(-50%);  float: none; display: inline-block; left: 50%; }
}

.cb-review-box {
    .cb-review-title { font-size: 24px; }
}

.cb-review-box .cb-review-area {
  padding: 20px 30px 30px;
}

/******************************************************************
REVIEW EXTERNAL STYLES
******************************************************************/

.cb-review-ext-box {width: 85px; height: 85px;
  .cb-percent-sign {font-size:16px; }
  .cb-score { font-size: 26px;}
}

.cb-review-ext-box.cb-small-box { width: 40px; height: 40px; }

/******************************************************************
AUTHOR PAGE STYLES
******************************************************************/
.cb-author-details.cb-sidebar {
  h3 a { font-size: 22px; }
}

/*------------------------------------------------------------------
[24. Modals]
*/
/******************************************************************
MODAL STYLES
******************************************************************/

.cb-modal.cb-s-modal { max-width: 920px; }
.cb-modal.cb-m-modal { max-width: 1020px;
  iframe[src*="youtube.com"], iframe[src*="vimeo"] { width: 1020px; height: 575px; }
}

.cb-s-modal {

  .cb-sub-posts {

    li { width: 260px; margin-right: 30px; float: left;
      .cb-mask { display: block; }

    }
    .cb-article-3 { margin-right: 0; }
  }


}

.cb-sidebar-none-narrow.cb-embed-out {
  .aligncenter {  width: 990px!important; margin-left: -182px; }
}

.cb-embed-out .alignleft:not(.size-thumbnail) {
    margin: 0 30px 30px -100px;
}

#cb-top-menu {
  .cb-left-side, .cb-right-side {
    a { margin: 0 10px; }
  }
}
.woocommerce.cb-sidebar-none, .woocommerce-page.cb-sidebar-none {
  ul.products {

    li.product {
      margin: 0 2% 2em 0;
      width: 23.5%;
    }

    li.first {
      clear: both;
    }

    li.last {
      margin-right: 0;
    }

  }

  .cb-main {    width: 100%;   }

  .related,
  .cart-collaterals {

    ul.products,
    ul {

      li.product {
        width: 48%;
      }
    }
  }
}
