.cb-gallery-post-wrap { 
    .cb-caption { bottom: 0; font-size: 14px; right: 0; padding: 10px 30px; position: absolute; background: rgba(255, 255, 255, 0.8); max-width: 80%; color: #000; }
    .cb-slider-arrow { color: #fff;  z-index: 10; position: absolute; top: 50%; -webkit-transform: translateY(-50%); -ms-transform: translateY(-50%); transform: translateY(-50%); border: 3px solid #fff; font-size: 40px; padding: 7px 0;  width: 60px; text-align: center;
        &:hover { cursor: pointer; box-shadow: 3px 3px 0 #161616;     background: rgba(255, 255, 255, 0.25); }
     }
    .cb-slider-arrow-p { /*rtl:ignore*/left: 40px; /*rtl:ignore*/margin-right: 20px; }
    .cb-slider-arrow-n { /*rtl:ignore*/right: 40px; /*rtl:ignore*/margin-left: 20px;}
}


.cb-s-modal {
	.cb-style-1 {
		.cb-mask {
			img { @include transition(0.35s opacity);
				&:hover { opacity: 0.7; }
			}
		}
	}
}

.cb-modal.cb-m-modal { max-width: 750px;
  iframe[src*="youtube.com"], iframe[src*="vimeo"] { width: 750px; height: 422px; }
}

/*********************
AUTHOR BOX STYLE
*********************/
#cb-author-box {
	.cb-mask { display:block;
		img {@include transition(all 0.35s);}
		&:hover img { opacity:0.9; }
	}
	.cb-meta { padding-left: 20px;
		.fn:hover { text-decoration: underline; }
	}
}

#cb-related-posts-block  {
	.slides {
		li { width:48%;	}
		.cb-no-1 { margin-right: 2%; }
		.cb-no-2 { margin-left: 2%; }
	}
	.cb-post-title { font-size: 18px; padding: 10px 0; }
	#cb-related-posts {
		.cb-no-1 {}
		.cb-no-2 { margin-left: 0; }

	}
}

.cb-style-1 {

	.cb-mask {
		img { @include transition(all 0.35s);
			&:hover { opacity: 0.6;  }
		}

	}

}

.cb-arrows-tr {
	.flex-direction-nav {
		li, a { @include transition(0.35s all); }
		li:hover {
			a { color: #fff; background: #161616; background: rgba(23, 23, 23, 0.9); }
		}
		.flex-disabled:hover { cursor: default; color: #161616;  background: #fff; background: rgba(255, 255, 255, 0.8) }

	}
}

/******************************************************************
FEATURED IMAGE STYLES
******************************************************************/

.cb-fis-block-site-width {  height: 400px; }
.cb-fis {
	h1 { font-size: 20px; }
	.cb-byline { font-size: 10px; letter-spacing: 3px;  }
}

.cb-fis-big {
  .cb-meta {
     h1 { font-size: 30px;  }

  }
}

.cb-fis-block-standard {
	.cb-byline { letter-spacing: 1px; }
}


.cb-fis-block-video {
	.cb-meta { padding: 20px 0; }
}
.cb-fis-b-standard {
	.cb-fis-block-video {
		.cb-meta { padding: 20px 0; }

	}
}

.cb-review-box {
	.cb-review-area { padding: 20px; }
	.cb-bar{

		.cb-user-rating-wrap {
			.cb-overlay:hover, .cb-overlay-stars:hover {cursor: pointer; }
		}
		.cb-voted {.cb-overlay:hover, .cb-overlay-stars:hover { cursor: auto!important; }}
	}

}

/******************************************************************
EXTRA STYLES
******************************************************************/
.fa.cb-times { font-size: 28px;
	&:hover { cursor: pointer; }
  &:after { left: -5px; }
}

.cb-underline-h .cb-meta a:hover {
	border-bottom: 1px solid #999;
}

/******************************************************************
POST CONTENT
******************************************************************/
.cb-entry-content {
  .alignleft {   margin: 0 30px 30px 0; max-width: 50%;  }
  .alignright {   margin: 0 0 30px 30px; max-width: 50%;  }

}

.cb-sidebar-none-narrow.cb-embed-out {
	.aligncenter { max-width: none;  width: 750px!important; margin-left: -145px; margin-right: -145px; }
}

/*------------------------------------------------------------------
[19. Modals]
*/
/******************************************************************
MODAL STYLES
*****************************************************************/

.cb-modal { max-width: 450px;
	a:hover {text-decoration: underline; }
	.cb-trigger-log:hover, .cb-trigger-reg:hover { text-decoration: none; }

	.cb-close-lwa { padding-bottom: 10px; }
	input, .cb-subm { font-size: 16px; height: 50px; letter-spacing: 2px;  line-height: 50px; }

	.cb-modal-title { margin-bottom: 30px; font-size: 18px; letter-spacing: 3px;
		a { color :#5A5A5A;  margin: 0 20px; }
		.cb-active { padding-bottom: 6px;   }
	}


}
.cb-modal .cb-modal-inner { padding: 40px; }

.cb-s-modal {
	.cb-results-title {	letter-spacing: 3px; font-size: 20px; margin: 30px 0; }
	.cb-sub-posts { margin: 30px 0;
		li .cb-meta { padding: 0 10px 20px; }
	}
	.cb-search-field { height: 80px; line-height: 80px; letter-spacing: 3px;  }

	.cb-byline-short { margin-top: 5px; }
}

.cb-lwa-modal {
	.cb-submit { margin-top: 30px; }

	.cb-lwa-logo {
		img { max-height: 75px; }
	}
}