/*********************
GENERAL STYLES
*********************/
body {font-size: 14px; line-height: 1.8; -webkit-font-smoothing: antialiased; color: #484848; }

img:not(.boxer-image) { max-width: 100%; }

.cb-circle {-webkit-border-radius: 50%; -moz-border-radius: 50%; border-radius: 50%; }

.cb-link-overlay { position: absolute; z-index: 15; top: 0; left: 0; bottom: 0; right: 0; max-height: 100%; max-width: 100%;   @include transition(0.5s opacity ease); }

.cb-sidebar {padding: 20px; }

.cb-pre-load > div, .cb-pre-load > ul, .cb-pre-load > article, .cb-infinite-load a { opacity:1; @include transition(0.25s opacity ease); -moz-transition: none; }

.cb-pre-load:before { margin-top: -30px; margin-left: -20px; font-family: 'fontawesome'; content: '\f110'; -webkit-animation: cb-spinner 2.5s infinite linear; -moz-animation: cb-spinner 2.5s infinite linear; animation: cb-spinner 2.5s infinite linear; position: absolute; top: 50%; left: 50%; font-size: 40px; z-index: 1; opacity: 0; @include transition(0.25s opacity ease);  }

.cb-main.cb-pre-load:before { content: ''; }

.cb-slide-up {-webkit-transform: translate(0, 180px); transform: translate(0, 180px); -webkit-animation: cb-slide-reset 0.5s ease forwards; animation: cb-slide-reset 0.5s ease forwards; }
.cb-slide-up-subtle {-webkit-transform: translate(0, 30px); transform: translate(0, 30px); -webkit-animation: cb-slide-reset 0.5s ease forwards; animation: cb-slide-reset 0.5s ease forwards; }
.cb-slide-to-r {-webkit-transform: translate(-20px, 0); transform: translate(-20px, 0); -webkit-animation: cb-slide-reset 0.5s ease forwards; animation: cb-slide-reset 0.5s ease forwards; }
.cb-slide-opa {opacity: 0; -webkit-animation: cb-slide-up-opa 0.5s ease forwards; animation: cb-slide-up-opa 0.5s ease forwards; }
.cb-slide-up-opa .cb-main, .cb-slide-up-opa > .cb-fis { opacity: 0; -webkit-transform: translate(0, 200px); transform: translate(0, 200px); -webkit-animation: cb-slide-up-opa 0.5s ease forwards; animation: cb-slide-up-opa 0.5s ease forwards; }

.cb-dark-loader.cb-pre-load:before {   color: #fff;  }
.cb-light-loader.cb-pre-load:before {   color: #161616;  }

.cb-pre-load.cb-pro-load:before {  opacity:1;   }
.cb-pre-load.cb-pro-load > div, .cb-pre-load.cb-pro-load > ul, .cb-pre-load.cb-pro-load > form, .cb-pre-load.cb-pro-load > article, .cb-infinite-load .cb-pre-load { opacity: 0.15!important; }

.cb-hidden { display: none; }
.cb-mask{ margin: 0; position:relative; overflow: hidden; }
#cb-bg-to{ top: 0; left:0; position: fixed; display: inline; width: 100%; cursor: pointer; height:100%; }
#cb-container { @include transition(0.35s ease opacity); }
.cb-bg-to-on {
	#cb-container, #cb-footer, #cb-top-menu { position: relative; }
}

.cb-alp-ld { position: relative; display: block; height: 100px; text-align: center; 
	i {  -webkit-animation: fa-spin 1.2s infinite linear;  animation: fa-spin 1.2s infinite linear; }
	.cb-end { display: none;     text-transform: uppercase;}
 }
 .cb-no-more {
 	i { display: none; }
 	.cb-end { display: block; }
 }

article.sticky .cb-mask:before {content: "\f08d"; font-family: fontAwesome; position: absolute; top: 0; z-index: 15; left: 0; color: #666; background: rgba(255, 255, 255, 0.9); padding: 15px; }

.cb-read-more {text-transform: uppercase; padding-left: 3px; -webkit-transition: 0.35s background, 0.35s color; -transition: 0.35s background, 0.35s color; transition: 0.35s background, 0.35s color; font-size: 10px; letter-spacing: 1px; display: inline-block; font-weight: 700; }

