/*********************
ANIMATION
*********************/
@-webkit-keyframes cb-slide-reset {
	0% { }
	100% { -webkit-transform: translate(0, 0); }
}

@keyframes cb-slide-reset {
	0% { }
	100% { -webkit-transform: translate(0, 0); transform: translate(0, 0); }
}



@-webkit-keyframes cb-slide-up-opa {
	0% { }
	100% { -webkit-transform: translate(0, 0);  opacity:1;  }
}

@keyframes cb-slide-up-opa {
	0% { }
	100% { -webkit-transform: translate(0, 0); transform: translate(0, 0);   opacity:1; }
}

@-moz-keyframes cb-spinner {
    from { -moz-transform: rotate(0deg); }
    to { -moz-transform: rotate(360deg); }
}
@-webkit-keyframes cb-spinner {
    from { -webkit-transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); }
}
@keyframes cb-spinner {
    from {transform:rotate(0deg);}
    to {transform:rotate(360deg);}
}

.cb-ani-fade-in-2-5s {
	opacity: 0;
	animation: cb-ani-fade-in 2.5s ease forwards;
	-webkit-animation: cb-ani-fade-in 2.5s ease forwards;
}

.cb-ani-fade-in-1s {
	opacity: 0;
	animation: cb-ani-fade-in 1s ease forwards;
	-webkit-animation: cb-ani-fade-in 1s ease forwards;
}

.cb-ani-fade-in-1-5s {
	opacity: 0;
	animation: cb-ani-fade-in 1.5s ease forwards;
	-webkit-animation: cb-ani-fade-in 1.5s ease forwards;
}

.cb-ani-fade-in-0-5s {
	opacity: 0;
	animation: cb-ani-fade-in 0.5s ease forwards;
	-webkit-animation: cb-ani-fade-in 0.5s ease forwards;
}

@keyframes cb-ani-fade-in {
	to { opacity:1; }
}

@-webkit-keyframes cb-ani-fade-in {

	to { opacity:1; }

}

@-moz-keyframes cb-ani-fade-in {
	to { opacity:1; }
}

.cb-ani-fade-out {
	opacity:1;
	animation: cb-ani-fade-out 2.5s ease forwards;
	-webkit-animation: cb-ani-fade-out 2.5s ease forwards;
}

.cb-ani-fade-out-fast {
	opacity:1;
	animation: cb-ani-fade-out 0.5s ease forwards;
	-webkit-animation: cb-ani-fade-out 0.5s ease forwards;
}

@keyframes cb-ani-fade-out {
	to { opacity: 0; }
}

@-webkit-keyframes cb-ani-fade-out {

	to { opacity: 0; }

}

@-moz-keyframes cb-ani-fade-out {
	to { opacity: 0; }
}

.cb-fade {
	@include transition(opacity 0.5s);
}

.cb-fade-in {
	opacity:1;
}
.cb-fade-out {
	opacity: 0;
}

.cb-fade-to-03 {
	animation: cb-ani-fade-to-03 3s ease both;
	-webkit-animation: cb-ani-fade-to-03 3s ease both;
}

@-webkit-keyframes cb-ani-fade-to-03 {
	to { opacity: 0.3; }
}

@keyframes cb-ani-fade-to-03 {
	to { opacity: 0.3; }
}


@-webkit-keyframes cb-ani-fly-top {
	from { -webkit-transform: translateY(100%); }
}

@keyframes cb-ani-fly-top {
	from { transform: translateY(100%); }
}

@-webkit-keyframes cb-ani-fly-bot {
	from { -webkit-transform: translateY(-100%); }
}

@keyframes cb-ani-fly-bot {
	from { transform: translateY(-100%); }
}

.cb-z-999 {
	z-index: 999;
}

/*********************
WRAPPER
*********************/

.wrap, .cb-wrap-off #cb-content { width: 100%; margin: 0 auto; }

.cb-pb .cb-full-width { padding: 0 0 30px 0; }
.cb-box-light .wrap { box-shadow: -1px 0px 0px 0px #ddd, 1px 0px 0px 0px #ddd; -moz-box-shadow: -1px 0px 0px 0px #ddd, 1px 0px 0px 0px #ddd;  -webkit-box-shadow: -1px 0px 0px 0px #ddd, 1px 0px 0px 0px #ddd; }
.cb-box-heavy .wrap {  box-shadow: -1px 0px 0px 0px #888, 1px 0px 0px 0px #888; -moz-box-shadow: -1px 0px 0px 0px #888, 1px 0px 0px 0px #888;  -webkit-box-shadow: -1px 0px 0px 0px #888, 1px 0px 0px 0px #888;  }
.cb-box-light.cb-nav-bar-wrap.wrap, .cb-box-light.cb-main-nav.wrap, .cb-box-light #cb-widgets.wrap, .cb-box-light .cb-footer-lower .wrap,
.cb-box-heavy.cb-nav-bar-wrap.wrap, .cb-box-heavy.cb-main-nav.wrap, .cb-box-heavy #cb-widgets.wrap, .cb-box-heavy .cb-footer-lower .wrap { box-shadow: none!important; -moz-box-shadow: none!important;  -webkit-box-shadow: none!important;  }

/*********************
LINK STYLES
*********************/
a {	color: #444; text-decoration: none;
	&:hover, &:focus { color: #000; }
	&:link { -webkit-tap-highlight-color : rgba( 0, 0, 0, 0.3 ); }
}
a:visited { text-decoration: none; }

/******************************************************************
H1, H2, H3, H4, H5 STYLES
******************************************************************/

h1, h2, h3, h4, h5 {
	font-family: $sans-serif;
	text-rendering: optimizelegibility;
	font-weight: 700;
	line-height: 1.3;

	a {
		text-decoration: none;
	}
}

h1 {
	font-size: 3em;
}

h2 {
	font-size: 2.5em;
}

h3 {
	font-size: 1.5em;
}

h4 {
	font-size: 1.3em;
	font-weight: 700;
}

h5 {
	font-size: 0.846em;
	text-transform: uppercase;
	letter-spacing: 2px;
