.cb-mob-menu { 
	/*rtl:ignore*/
	transform: translateX(-100%); 

	-webkit-transition: -webkit-transform 0.5s ease; -moz-transition: -moz-transform 0.5s ease; -o-transition: -o-transform 0.5s ease; transition: transform 0.5s ease; width: 100%; height: 100%; position: fixed; top: 0; right: 0; background: #161616; z-index: 110; overflow-y: auto; -webkit-overflow-scrolling: touch; max-width: 100%;

