/******************************************************************
POST FORMATS
******************************************************************/
/* Slider */
.slick-slider {position: relative; display: block; box-sizing: border-box; -webkit-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -webkit-touch-callout: none; -khtml-user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }
.slick-list {position: relative; display: block; overflow: hidden; margin: 0; padding: 0;     height: inherit; }
.slick-list:focus{    outline: none;}
.slick-list.dragging{    cursor: pointer;    cursor: hand;}
.slick-slider .slick-track,
.slick-slider .slick-list { transform: translate3d(0, 0, 0); }
.slick-track {position: relative; top: 0; left: 0; display: block;     height: inherit; }
.slick-track:before,
.slick-track:after {display: table; content: ''; }
.slick-track:after{    clear: both; }
.slick-loading .slick-track{    visibility: hidden; }
.slick-slide { background: #000;  display: none; float: left; height: 100%; min-height: 1px;     height: inherit; position: relative;  &:focus { outline: 0; }}
[dir='rtl'] .slick-slide {    float: right; }
.slick-slide img {  display: block; height: calc(100vh + 1px ); width: auto; opacity: 0; 

}
.slick-slide.slick-loading img {    display: none; }
.slick-slide.dragging img {    pointer-events: none; }
.slick-initialized .slick-slide {    display: block;}
.slick-loading .slick-slide{    visibility: hidden;}
.slick-vertical .slick-slide {display: block; height: auto; border: 1px solid transparent; }
.slick-arrow.slick-hidden {   display: none;}
.cb-slider-hidden img, .cb-slider-hidden span { opacity: 0; }

.cb-gallery-post-wrap { height: 100vh; background: #000; 
    .cb-caption { bottom: 0; font-size: 14px; right: 0; padding: 5px 10px; position: absolute; background: #fff; background: rgba(255, 255, 255, 0.8); max-width: 80%; color: #464646; opacity: 0; }
    .cb-meta { visibility: visible; @include transition(opacity 0.35s ease-in-out, visibility 0.35s ease-in-out); position: absolute; top: 50%; left: 50%; -webkit-transform: translate(-50%,-50%); -ms-transform: translate(-50%,-50%); transform: translate(-50%,-50%); z-index: 10; width: 80%; }
    img { @include transition(0.5s ease-in-out opacity, 0.5s transform ease-in-out);     height: inherit; }

    .cb-slider-image { border-left: 1px solid transparent; border-right: 1px solid transparent; }
    .slick-initialized article { display: block; }
}

.cb-arrows-hover .cb-gallery-post-wrap {
    .cb-slider-ldd img { opacity: 1!important;  } 
    .cb-meta { visibility: hidden; opacity: 0; }
}

.slick-slider { height: inherit; 
    .cb-slider-ldd { border-left-color: #fff; border-right-color: #fff;
      .cb-caption { opacity :1;}
     img { opacity: 0.7; }
   }
}

