	#cb-gallery-post, .cb-slideshow-wrap {     position: absolute; top: 0; width: 100%; height: 100%; } 
}
  
.cb-video-overlay-icon { color: #161616; background: #fff; background: rgba(255, 255, 255, 0.7); height: 60px; width: 60px; line-height: 60px; display: inline-block;  }

.cb-module-block {
	.format-video, .format-audio, .format-gallery {
		.cb-mask a:after { font-family: 'FontAwesome';  color: #161616; background: #fff; background: rgba(255, 255, 255, 0.7); height: 60px; width: 60px; position: absolute; top: 50%; left: 50%; text-align: center; line-height: 60px; border-radius: 50% 50%; margin-left: -30px; margin-top: -30px; }
	}
	.format-video, .format-audio {
